html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.grid-root {
  position: relative;
  min-height: 100%;
  padding-bottom: 10rem;
}

/* ------------------------------- */

nav {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
}

.nav-grid-root {
  justify-content: center;
}

.nav-grid-title {
  text-align: left;
}

.nav-grid-menu {
  width: 40rem;
  align-self: center;
}

.nav-menu {
  float: right;
}

.nav-text {
  text-decoration: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: black;
}

.nav-text-title {
  float: left;
  font-size: 24px;
  font-weight: 700;
}

.nav-text-menu {
  margin-inline: 1rem;
  float: right;
  font-size: 18x;
  font-weight: 350;
  color: #000000;
  transition: 0.3s;
}

.nav-text-menu:hover {
  color: #9e9e9e;
}

/* ------------------------------- */

.top {
  position: fixed;
  right: 10px;
  bottom: 10px;
  cursor: pointer;
}

/* ------------------------------- */

.footer {
  /* position: fixed; */
  bottom: 0;
  width: 100%;
  height: 5rem;
  background-color: #9e9e9e;
  text-align: center;
}

/* ------------------------------- */

.home-grid-root {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5rem;
  max-width: 75rem;
}

.home-grid-profile {
  padding-inline: 1rem;
}

.home-grid-introduce {
  padding-inline: 1rem;
}

.home-text {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.home-img-profile {
  height: 15rem;
  border-radius: 50%;
}

.home-img-social {
  margin-right: 1rem;
  opacity: 0.5;
  transition: 0.3s;
}

.home-img-social:hover {
  opacity: 1;
}

/* ------------------------------- */

.about-grid-root {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5rem;
  max-width: 75rem;
  text-align: center;
}

.about-grid-side {
  text-align: left;
}

.about-img-myphoto {
  width: 80%;
}
